import React from 'react';
import { graphql } from 'gatsby';
import { GatsbySeo } from 'gatsby-plugin-next-seo';
import Img from 'gatsby-image';

import Layout from '../../components/Layout';
import Container from '../../components/Container';
import Footer from '../../components/Footer';

import BlockCaseStudyCards from '../../components/BlockCaseStudyCards';
import BlockCTA from '../../components/BlockCTA';
import BlockHeaderNoImage from '../../components/BlockHeaderNoImage';
import BlockSteps from '../../components/BlockSteps';

export const pageQuery = graphql`
  {
    simpleTire: file(name: { eq: "simple-tire" }) {
      childImageSharp {
        fluid(maxWidth: 520) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    simpleTireSmall: file(name: { eq: "simple-tire-small" }) {
      childImageSharp {
        fluid {
          ...GatsbyImageSharpFluid
        }
      }
    }
    companies: allContentfulCompany {
      edges {
        node {
          name
          image {
            fluid(maxWidth: 150) {
              ...GatsbyContentfulFluid
            }
          }
        }
      }
    }
    OndemandTiresImage: contentfulCompany(name: { eq: "Ondemand tires" }) {
      image {
        fixed(width: 130) {
          ...GatsbyContentfulFixed
        }
      }
    }
    LEDMyplaceImage: contentfulCompany(name: { eq: "LEDMyplace" }) {
      image {
        fixed(width: 130) {
          ...GatsbyContentfulFixed
        }
      }
    }
    ikioImage: contentfulCompany(name: { eq: "ikio" }) {
      image {
        fixed(width: 130) {
          ...GatsbyContentfulFixed
        }
      }
    }
    supportGrowthImage: file(name: { eq: "support-growth" }) {
      childImageSharp {
        fluid(maxWidth: 480) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    repeatCustomersImage: file(name: { eq: "repeat-customers" }) {
      childImageSharp {
        fluid(maxWidth: 480) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    biggerBuyersImage: file(name: { eq: "bigger-buyers" }) {
      childImageSharp {
        fluid(maxWidth: 520) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    commonLivingSmall: file(name: { eq: "common-living-small" }) {
      childImageSharp {
        fluid(maxWidth: 244) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    homeFurnishingsSmall: file(name: { eq: "home-furnishings-small" }) {
      childImageSharp {
        fluid(maxWidth: 244) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    companies: allContentfulCompany {
      edges {
        node {
          name
          image {
            fixed(width: 150) {
              ...GatsbyContentfulFixed
            }
          }
        }
      }
    }
  }
`;

export default function nowvac({ data }) {
  return (
    <Layout>
      <GatsbySeo title="nowvac" />
      <Container className="case-study-page">
        <BlockHeaderNoImage
          pretitle="CASE STUDIES / NOWVAC"
          pretitleImage={data.supportGrowthImage.childImageSharp.fluid}
          title="A marketplace that gets customers pumped"
          desc={
            <>
              Nowvac is taking the industrial and scientific vacuum industry
              into the future with a modern, transparent B2B marketplace, and
              proving that buying high-end vacuum tech doesn't have to suck.
            </>
          }
          maxWidth="700"
        />

        <Img
          fluid={data.simpleTireSmall.childImageSharp.fluid}
          imgStyle={{ objectFit: 'cover' }}
          alt="wide"
          className="app__wide-image"
        />

        <BlockSteps
          pretitle="The brief"
          title="Nowvac's objective was twofold: create an easy-to-search marketplace where vendors of specialized industrial and scientific vacuum gear could reach thousands of untapped customers; and bring an industry dominated by brick-and-mortar giants online."
          data={[
            {
              title:
                "Translate Nowvac's considerable know-how into an enticing online marketplace and community for vendors and buyers accustomed to offline sales",
            },
            {
              title:
                "Break the market stronghold of established distributors who've been around for decades",
            },
            {
              title:
                'Provide an Amazon-like experience catering to the unique buying journey for advanced vacuum tech',
            },
            {
              title:
                'Enable flexible product data modelling to meet the specific needs of various vendors and their highly specialized products',
            },
          ]}
        />

        <Img
          fluid={data.simpleTireSmall.childImageSharp.fluid}
          imgStyle={{ objectFit: 'cover' }}
          alt="wide"
          className="app__wide-image"
        />

        <BlockSteps
          wide={true}
          pretitle="The solution"
          title="A modern B2B marketplace that seamlessly integrates with inventory and product information systems, primed to help Nowvac succeed today—and tomorrow."
          data={[
            {
              title: 'Streamlined operations with extensible API',
              text:
                "A multi-vendor marketplace requires custom data models and user interfaces. Because Swell's API allows vendors to manage their own data models without hiring database admins, Nowvac scales its fast-growing marketplace without the headache of infrastructure maintenance.",
            },
            {
              title: 'Less time to launch, using out-of-the-box features',
              text:
                'Nowvac were able to work with Swell to build and launch their marketplace in 3 months, as compared to their previous 12 month struggle to launch. Through knowledgeable account managers and technical support, the Swell team were able to help Nowvac identify roadblocks in their project roadmap and accelerate their time to market.',
            },
            {
              title:
                'Critical PIM system integration for easy product management',
              text:
                'A lot of products means a lot of data. Nowvac was able to seamlessly integrate an adaptable product information management (PIM) system with Swell, allowing their network of vendors to access and update their product information anytime, anywhere.',
            },
            {
              title: 'Complex multi-vendor marketplace features, simplified',
              text:
                "Nowvac's ecommerce vision had a lot of moving parts: vendors, products, outside integrations, split payouts, and more. Swell was able to address all of these needs in one platform, enabling Nowvac to engage their community exactly how they envisioned.",
            },
            {
              title: 'Responsive, ongoing support from a team that gets it',
              text:
                'Nowvac has a startup mentality. That means tight timelines and changes in direction that demand agility. The Swell team answers this with meaningful, ongoing support—answering questions, offering suggestions, and enabling an adaptable marketplace—all for less time, money, and effort.',
            },
          ]}
        />

        <h4 className="middle-page-title">
          150 vendors
          <br />
          650+ SKUs
          <BlockCTA
            align="center"
            secondaryButtonTo="/case-studies"
            secondaryButtonText="Visit the nowvac marketplace"
          />
        </h4>

        <h4 className="middle-page-title">More case studies</h4>
        <BlockCaseStudyCards
          items={[
            {
              title: 'HYPERIKON',
              description:
                'How Hyperikon multiplied their b2b sales and reduced their deal closing time with Net Terms automation.',
              link: '/case-studies/hyperikon',
              image: data.biggerBuyersImage.childImageSharp.fluid,
            },
            {
              title: 'DTC ETHICAL DIAMONDS',
              description:
                'Great Heights reinvented the way conscious couples commit to forever',
              link: '/case-studies/nowvac',
              image: data.biggerBuyersImage.childImageSharp.fluid,
            },
            {
              title: 'DTC ETHICAL DIAMONDS',
              description:
                'Great Heights reinvented the way conscious couples commit to forever',
              link: '/case-studies/nowvac',
              image: data.biggerBuyersImage.childImageSharp.fluid,
            },
            {
              title: 'DTC ETHICAL DIAMONDS',
              description:
                'Great Heights reinvented the way conscious couples commit to forever',
              link: '/case-studies/nowvac',
              image: data.biggerBuyersImage.childImageSharp.fluid,
            },
          ]}
        />
      </Container>
      <Footer />
      <style jsx global>{`
        .container.case-study-page {
          padding: 0;
          max-width: 100%;
        }

        .app__wide-image {
          max-height: 300px;
        }

        .middle-page-title {
          text-align: center;
          font-size: 42px;
          font-weight: 600;
          margin-top: 0.75rem;
          margin-bottom: 50px;
        }

        .middle-page-title .app__actions {
          margin-top: 20px;
        }
      `}</style>
    </Layout>
  );
}
